'use client';

// 1. Import Custom Hook (Dari folder karyawan)
import { usePOSPage } from '@/hooks/karyawan/usePOSPage';

// 2. Import Komponen UI
import POSFilterBar from '@/components/karyawan/pos/POSFilterBar';
import POSProductGrid from '@/components/karyawan/pos/POSProductGrid';
import POSCart from '@/components/karyawan/pos/POSCart';
import POSCheckoutDialog from '@/components/karyawan/pos/POSCheckoutDialog';
import POSSuccessDialog from '@/components/karyawan/pos/POSSuccessDialog';

export default function POSPage() {
    // 3. Panggil Logika dari Hook
    const {
        // Data & Loading
        products,
        categories,
        payments,
        loading,
        
        // Cart & Total
        cart,
        totalCartPrice,
        
        // State UI & Form
        searchQuery,
        setSearchQuery,
        selectedCategory,
        setSelectedCategory,
        
        isCheckoutOpen,
        setIsCheckoutOpen,
        isProcessing,
        lastTransaction,
        setLastTransaction,
        
        selectedPayment,
        setSelectedPayment,
        customerName,
        setCustomerName,
        cashAmount,
        setCashAmount,

        // Actions
        addToCart,
        removeFromCart,
        updateQty,
        handleCheckout
    } = usePOSPage();

    return (
        <div className="flex flex-col lg:flex-row h-[calc(100vh-80px)] gap-4">
            
            {/* AREA KIRI: KATALOG PRODUK */}
            <div className="flex-1 flex flex-col gap-4 overflow-hidden">
                <POSFilterBar 
                    categories={categories}
                    selectedCategory={selectedCategory}
                    setSelectedCategory={setSelectedCategory}
                    searchQuery={searchQuery}
                    setSearchQuery={setSearchQuery}
                />
                <POSProductGrid 
                    loading={loading}
                    products={products}
                    onAddToCart={addToCart}
                />
            </div>

            {/* AREA KANAN: KERANJANG BELANJA */}
            <POSCart 
                cart={cart} 
                updateQty={updateQty}
                removeFromCart={removeFromCart}
                onCheckout={() => setIsCheckoutOpen(true)}
            />

            {/* MODAL CHECKOUT */}
            <POSCheckoutDialog 
                isOpen={isCheckoutOpen}
                onOpenChange={setIsCheckoutOpen}
                totalAmount={totalCartPrice}
                payments={payments}
                selectedPayment={selectedPayment}
                setSelectedPayment={setSelectedPayment}
                customerName={customerName}
                setCustomerName={setCustomerName}
                cashAmount={cashAmount}
                setCashAmount={setCashAmount}
                onProcess={handleCheckout}
                isProcessing={isProcessing}
            />

            {/* MODAL SUKSES (STRUK) */}
            <POSSuccessDialog 
                transaction={lastTransaction}
                onClose={() => setLastTransaction(null)}
            />
        </div>
    );
}